import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

const businessHours = [
  { day: 'Monday - Friday', hours: '9:00 AM - 6:00 PM' },
  { day: 'Saturday', hours: '10:00 AM - 4:00 PM' },
  { day: 'Sunday', hours: 'Closed' },
];

const contactMethods = [
  {
    title: 'Email',
    value: 'contact@deventerprises.com',
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
      </svg>
    ),
  },
  {
    title: 'Phone',
    value: '+1 (555) 123-4567',
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
      </svg>
    ),
  },
  {
    title: 'Address',
    value: '123 Tech Street, Silicon Valley, CA 94000',
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
      </svg>
    ),
  },
];

export default function Contact() {
  return (
    <div className="min-h-screen bg-[#f9fafb] text-gray-900">
      <Navbar />
      
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 md:py-12">
        {/* Hero Section */}
        <section className="text-center mb-12 md:mb-16">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            Contact Us
          </h1>
          <p className="text-lg md:text-xl text-gray-600 max-w-2xl mx-auto">
            We're here to help. Get in touch with us for any questions or inquiries.
          </p>
        </section>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 md:gap-12">
          {/* Contact Information */}
          <div className="space-y-8">
            <div>
              <h2 className="text-2xl md:text-3xl font-bold text-gray-900 mb-6">
                Get in Touch
              </h2>
              <p className="text-gray-600 mb-8 leading-relaxed">
                Whether you have a question about our products, need technical support, 
                or want to discuss a custom solution, our team is ready to assist you.
              </p>
            </div>

            {/* Contact Methods */}
            <div className="space-y-4">
              {contactMethods.map((method, index) => (
                <div
                  key={index}
                  className="bg-white rounded-lg p-6 border border-gray-200 hover:border-[#3b82f6] hover:shadow-md transition-all shadow-sm"
                >
                  <div className="flex items-start gap-4">
                    <div className="text-[#3b82f6] flex-shrink-0 mt-1">
                      {method.icon}
                    </div>
                    <div>
                      <h3 className="text-lg font-semibold text-gray-900 mb-1">
                        {method.title}
                      </h3>
                      <p className="text-gray-600">
                        {method.value}
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Business Hours */}
            <div className="bg-white rounded-lg p-6 border border-gray-200 shadow-sm">
              <h3 className="text-xl font-semibold text-gray-900 mb-4">
                Business Hours
              </h3>
              <div className="space-y-3">
                {businessHours.map((schedule, index) => (
                  <div key={index} className="flex justify-between items-center">
                    <span className="text-gray-700">{schedule.day}</span>
                    <span className="text-[#3b82f6] font-medium">{schedule.hours}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Additional Information */}
          <div className="space-y-8">
            <div>
              <h2 className="text-2xl md:text-3xl font-bold text-gray-900 mb-6">
                Why Choose Us?
              </h2>
              <div className="space-y-4">
                <div className="bg-white rounded-lg p-6 border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-gray-900 mb-2">
                    Expert Support
                  </h3>
                  <p className="text-gray-600 leading-relaxed">
                    Our technical team is available to help you find the right solutions 
                    for your specific needs.
                  </p>
                </div>
                <div className="bg-white rounded-lg p-6 border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-gray-900 mb-2">
                    Fast Response
                  </h3>
                  <p className="text-gray-600 leading-relaxed">
                    We typically respond to inquiries within 24 hours during business days.
                  </p>
                </div>
                <div className="bg-white rounded-lg p-6 border border-gray-200 shadow-sm">
                  <h3 className="text-lg font-semibold text-gray-900 mb-2">
                    Custom Solutions
                  </h3>
                  <p className="text-gray-600 leading-relaxed">
                    Need something specific? We work with you to create tailored solutions 
                    that meet your exact requirements.
                  </p>
                </div>
              </div>
            </div>

            {/* Note about Footer Form */}
            <div className="bg-gradient-to-r from-[#3b82f6] to-[#14b8a6] rounded-lg p-6 md:p-8 shadow-lg">
              <h3 className="text-xl font-semibold text-white mb-3">
                Send Us a Message
              </h3>
              <p className="text-white/90 mb-4 leading-relaxed">
                You can also use the contact form in the footer to send us a message directly. 
                We'll get back to you as soon as possible.
              </p>
              <a
                href="#footer"
                className="inline-block bg-white text-gray-900 px-6 py-2 rounded-lg font-semibold hover:bg-gray-50 transition-all shadow-md hover:shadow-lg"
              >
                Go to Contact Form
              </a>
            </div>
          </div>
        </div>
      </main>

      <div id="footer">
        <Footer />
      </div>
    </div>
  );
}

